<template>
  <div class="app-container">
    <el-card class="box-card box-margin" style="margin-bottom: 20px;">
      <div class="searchCont">
        <div class="searchLeft">
          <el-input size="small" clearable placeholder="企业名称" v-model.trim="messageForm.toCompanyName" @keyup.enter.native="goSearch"></el-input>
          <el-button type="primary" size="small" icon="el-icon-search" @click="goSearch">搜索</el-button>
        </div>
        <div class="searchRig">
          <el-button type="primary" size="small" @click="goSelectCompany">新建短信</el-button>
        </div>
      </div>
    </el-card>
    <el-card class="box-card box-margin">
      <div slot="header" class="clearfix headerTop">
        <el-select size="small" filterable v-model="messageForm.deptIds" placeholder="选择部门" multiple clearable @change="deptmentChange">
          <el-option
            v-for="item in deptmentList"
            :key="item.deptId"
            :label="item.deptName"
            :value="item.deptId">
          </el-option>
        </el-select>
        <el-select filterable size="small" v-model="messageForm.userIds" placeholder="选择人员" multiple clearable @change="userChange">
          <el-option
            v-for="item in salePersonList"
            :key="item.userId"
            :label="item.userName"
            :value="item.userId">
          </el-option>
        </el-select>
      </div>
      <div>
        <el-table :data="messageCompanyList" border fit highlight-current-row ref="messageCompanyList">
<!--          <el-table-column type="selection" width="55"></el-table-column>-->
          <el-table-column align="center" min-width="160px" label="接收企业">
            <template slot-scope="scope">
              <span style="color: #05AAFF;cursor: pointer;" @click="goCompanyDatail(scope.row)">{{scope.row.companyName}}</span>
            </template>
          </el-table-column>
          <el-table-column align="center" label="短信模板">
            <template slot-scope="scope">
              <span>{{scope.row.smsTemplate}}</span>
            </template>
          </el-table-column>
          <el-table-column align="center" label="短信类型">
            <template slot-scope="scope">
              <span>{{scope.row.smsType == 0 ? '认证码' : scope.row.smsType == 1 ? '个人短信' : scope.row.smsType == 2 ? '政策推送' : scope.row.smsType == 3 ? '知识产权到期' : '-' }}</span>
            </template>
          </el-table-column>
          <el-table-column align="center" label="发送开始时间">
            <template slot-scope="scope">
              <span>{{scope.row.sendTime}}</span>
            </template>
          </el-table-column>
          <el-table-column align="center" label="发送人">
            <template slot-scope="scope">
              <span>{{scope.row.sendUserName}}</span>
            </template>
          </el-table-column>
          <el-table-column align="center" label="操作" class-name="small-padding fixed-width">
            <template slot-scope="scope">
              <span class="spanButton" @click="openSendDetail(scope.row)">发送详情</span>
            </template>
          </el-table-column>
        </el-table>
        <div v-if="messageCompanyList.length != 0">
          <div class="noMore" @click="pageDown" v-if="total - messageCompanyList.length > 0">
            点击加载更多
          </div>
          <div class="noMore" v-else>没有更多数据了</div>
        </div>
      </div>
    </el-card>

    <el-dialog
      title="短信发送详情"
      :visible.sync="dialogVisible"
      width="50%"
      :before-close="handleClose">
      <div style="max-height: 400px;overflow: auto">
        <el-table :data="messageDetailList" border fit highlight-current-row ref="messageDetailList">
          <el-table-column align="center" min-width="160px" label="企业名称">
            <template slot-scope="scope">
              <span>{{scope.row.companyName}}</span>
            </template>
          </el-table-column>
          <el-table-column align="center" label="短信接收人">
            <template slot-scope="scope">
              <span>{{scope.row.userName}}</span>
            </template>
          </el-table-column>
          <el-table-column align="center" label="接收号码">
            <template slot-scope="scope">
              <span>{{scope.row.toMobile}}</span>
            </template>
          </el-table-column>
        </el-table>
        <div v-if="messageDetailList.length != 0">
          <div class="noMore" @click="pageDown" v-if="msgDetailTotal - messageDetailList.length > 0">
            点击加载更多
          </div>
          <div class="noMore" v-else>没有更多数据了</div>
        </div>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button size="small" @click="dialogVisible = false">取 消</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
  import {getDepartmentTree} from '@/api/department.js'
  import { queryUserComboInfo } from '@/api/customerCenter'
  import {queryMsgSmsSendList,queryMsgSmsReceiveList} from '@/api/messageCenter'
  export default {
    data(){
      return{
        messageForm:{
          page:0,
          pageSize:20,
        },
        deptmentList:[],
        salePersonList:[],
        messageCompanyList:[],
        messageDetailList:[],
        msgDetailTotal:0,
        total:0,
        dialogVisible:false,
        allowGetRoleUserList: this.authorityControl('SYSTEM:CUSTOMER:MANAGE','SYSTEM:CUSTOMER:CENTER','SYSTEM:CUSTOMER:ROLE:USER:LIST'),//获取指定角色用户列表
        msgDetailForm:{
          page:0,
          pageSize:20
        },
      }
    },
    activated() {
      this.getSendList();
      this.getCompanyDeptList();

      //判断上一次在哪里离开回到指定位置
      this.$nextTick(function(){
        let position = this.$store.getters.messageSendScroll //返回页面取出来
        // window.scroll(0, position)
        document.documentElement.scrollTop = document.body.scrollTop = position
      })
    },
    beforeRouteLeave(to, from, next){
      let position = document.documentElement.scrollTop
      this.$store.commit('SET_MESSAGESEND_SCROLL', position) //离开路由时把位置存起来
      this.$store.commit('SET_MESSAGESEND_PAGE', this.messageForm.page) //离开路由时把位置存起来
      next()
    },
    methods:{
      //获取短信发送记录列表
      getSendList(){
        queryMsgSmsSendList(this.messageForm).then(res=>{
          if(res.code == 200){
            if(this.messageForm.page > 0){
              this.messageCompanyList = this.messageCompanyList.concat(res.data.data)
            }else{
              this.messageCompanyList = res.data.data;
            }
            this.total = res.data.total
          }
        })
      },

      //分页
      pageDown(){
        this.messageForm.page = this.messageForm.page + 20
        this.getSendList()
      },

      // 获取左侧公司部门列表
      getCompanyDeptList() {
        getDepartmentTree({}).then(res => {
          if (res.code == 200) {
            this.deptmentList = res.data.deptInfoTreeVos;
          } else {
            this.$message.warning(res.message)
          }
        })
      },

      //获取信息详情
      getMsgDetailList(id){
        this.msgDetailForm.smsId = id
        queryMsgSmsReceiveList(this.msgDetailForm).then(res=>{
          if(res.code == 200){
            if(this.msgDetailForm.page > 0){
              this.messageDetailList = this.messageDetailList.concat(res.data.data)
            }else{
              this.messageDetailList = res.data.data;
            }

            this.msgDetailTotal = res.data.total;
          }
        })
      },

      //信息详情分页
      getMore(){
        this.msgDetailForm.page = this.msgDetailForm.page + 20;
        this.getMsgDetailList();
      },

      //根据角色获取人员列表
      getPersonList(name,deptIds) {
        let formData = new FormData;
        formData.append('roleNameList', name)
        if(deptIds != undefined){
          formData.append('deptIdList',deptIds)
        }
        if(this.allowGetRoleUserList.twoAllow){
          queryUserComboInfo(formData).then(res => {
            if (res.code == 200) {
              this.salePersonList = res.data;
            } else {
              this.$message.warning(res.message)
            }
          })
        }else{
          this.$message.warning('抱歉，您没有权限获取角色用户列表')
        }
      },

      deptmentChange(val){
        this.getPersonList('业务员,业务主管',val)
        this.messageForm.page = 0
        this.messageForm.pageSize = 20
        this.getSendList();
      },

      userChange(){
        this.messageForm.page = 0
        this.messageForm.pageSize = 20
        this.getSendList();
      },

      goSearch(){
        this.messageForm.page = 0
        this.messageForm.pageSize = 20
        this.getSendList();
      },

      //选择发送企业
      goSelectCompany(){
        this.$router.push({
          path: '/customer/messageMassSend/addNewMessage',
        })
      },

      //发送详情
      openSendDetail(row){
        this.dialogVisible = true;
        this.msgDetailForm.page = 0;
        this.getMsgDetailList(row.smsId);
      },
      handleClose(){
        this.dialogVisible = false;
      },

      //跳转到企业信息
      goCompanyDatail(data) {
        this.$router.push({'path': '/customer/editCustomer', 'query': {'id': data.companyId, 'check': true}})
      }
    }
  }
</script>

<style lang="less" scoped>
  .searchCont{
    display: flex;
    align-items: center;
    justify-content: space-between;
    .searchLeft{
      display: flex;
    }
  }

  /deep/ .el-input{
    width:auto;
    margin-right: 10px;
  }

  // 修改列表页面表格样式
  /deep/ .el-table {
    border: rgb(217, 242, 255) 1px solid !important;
  }

  /deep/ .el-table th.gutter {
    display: table-cell !important;
  }

  /deep/ .el-table--border th,
  /deep/ .el-table th {
    background: #2CB1F5 !important;
    color: #fff;
    font-weight: 400;
    border-right: 1px solid rgba(220, 223, 230, 0.21);
    padding: 7px 0;
  }

  /deep/ .el-table__row:nth-child(even) {
    background: rgb(235, 248, 255) !important;
    border: 1px solid rgba(0, 141, 214, 0.26666666666666666);
  }

  /deep/ .el-table--border td {
    border: none;
    box-sizing: border-box;
  }

  /deep/ .el-table--border td:first-child {
    border-right: rgb(217, 242, 255) 1px solid !important;
  }

  /deep/ .el-table--border td:last-child {
    border-left: rgb(217, 242, 255) 1px solid !important;
  }

  .spanButton {
    color: #05AAFF;
  }

  .shu {
    margin: 0 10px;
    color: #ccc;
  }

  .col_red {
    color: #FF6B6B !important;
  }

  .col_red:hover {
    opacity: 0.5;
  }

  .col_skyblue {
    color: #05AAFF !important;
  }

  .col_skyblue:hover {
    opacity: 0.5;
  }

  .colUpDown {
    color: #FD903C !important;
  }

  .colUpDown:hover {
    opacity: 0.5;
  }

  /deep/ .el-select{
    margin-right: 10px;
  }

  .headerTop{
    display: flex;
  }

  .noMore{
    text-align: center;
    font-size:14px;
    padding:10px 0;
    cursor: pointer;
  }
</style>



